import {useState,useEffect,useContext} from 'react';
import {Route,Link,BrowserRouter,useHistory} from 'react-router-dom';
import Home from './components/home'
import BookList from './components/Booklist'
import {AddBook} from './components/Addbook'
import Details from './components/Details'
import Register from './components/register'
import Login from './components/login'
import './App.css';
import FormDialog from './components/DropDown'


import BookContext from './components/BookContext'


function App(){
  const [user,setUser]=useState(false)

  const [loginFailed,setLoginFailed]=useState<string>("")
    const initialState = useContext(BookContext)
    console.log(initialState);
    

  // const history=useHistory();

  useEffect(()=>{
    if(localStorage.getItem("token")){
      setUser(true);
    }
  },[])

  const logout=async()=>{
    setUser(false);
    localStorage.clear();
  }

  const authentication=async (username:string,password:string)=>{
    
    let auth=await fetch("http://localhost:8000/users/login",{
      method:"POST",
      body:JSON.stringify({username:username,password:password}),
      headers:{"Content-Type":"application/json"}
    });
    
    // let token=await auth.json(); 
    if(auth.status===200){
      console.log("otp");
      
      // setUser(false);
      setLoginFailed("success");
      initialState.books.otp=true;
      initialState.books.open=true;


    }else{
      // localStorage.setItem("token",token);
      // setUser(true);
      setLoginFailed("failed");
    }
  }

  const handleOtp=async(otp:any)=>{
    let auth=await fetch("http://localhost:8000/users/otpverification",{
      method:"POST",
      body:JSON.stringify({otp:otp}),
      headers:{"Content-Type":"application/json"}
    });
    let token=await auth.json(); 

    if(token){
      localStorage.setItem("token",token);
      setUser(true);

    }
    
  }
  const handleNewUser=async(newUser:any)=>{
    console.log("reg")
    await fetch("http://localhost:8000/users/registration",{
    method:"POST",
    body:JSON.stringify(newUser),
    headers:{"Content-Type":"application/json"}
    
    })
  }

    return(
      <div>
      <BrowserRouter>
        <div>
        <h1>Book Management System</h1>
        <ul className="header">
          <li><Link to="/">Home</Link></li>
          <li><Link to="/books">Book List</Link></li>
          <li>{user?<Link to="/addbook">Add Books</Link>:null}</li>
          <li>{user?null:<Link to="/register">Register</Link>}</li>
          <li>{user?null:<Link to="/login">Login</Link>}</li>
          <li>{user?<Link to="/" onClick={()=>logout()}>LogOut</Link>:null}</li>
        </ul>
        <Route exact path="/" component={Home}></Route>
        <Route exact path="/books" component={BookList}></Route>
        <Route exact path="/addbook" component={AddBook}></Route>
        <Route exact path="/details/:id?" component={Details}>
        </Route>
        <Route exact path="/register">
          <Register handleregistration={(newUser:any)=>{handleNewUser(newUser)}}></Register>
        </Route>
        <Route exact path="/login">
        <Login valid={loginFailed} handlelogin={(username,password)=>{authentication(username,password)}}></Login>
        </Route>
        <Route exact path="/otp" component={FormDialog}></Route>
        {initialState.books.otp?<div><FormDialog handlesomething={(otp:any)=>handleOtp(otp)}></FormDialog></div>:null}
        </div>
      </BrowserRouter>
      </div>
    )
}
export default App;
