import React,{useState,useContext} from 'react';
// import {useHistory} from 'react-router-dom'
import {useHistory} from 'react-router-dom';
import { Button,Col, Form} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css'
import BookContext from './BookContext';


interface props {
    valid:string,
  handlelogin: (username:string,password:string) => void
}
const Login:React.FC<props> = (props: any) => {
  const [username, setUserName] = useState("");
  const [password, setPassword] = useState("");
  const initialState =useContext(BookContext)
  const history = useHistory();



  function inputlusername(e: any) {
    setUserName(e.target.value)
  }
  function inputlpassword(e: any) {
    setPassword(e.target.value)
  }
    return (<>

<Form>
  <Form.Group as={Col} controlId="formGridEmail">     
    <Form.Label>User Name </Form.Label>
    <Form.Control type="email" placeholder="Enter email" onChange={inputlusername}/>
  </Form.Group>
  <Form.Group as={Col} controlId="formGridPassword" >
    <Form.Label>Password</Form.Label>
    <Form.Control type="password" placeholder="Password" onChange={inputlpassword}/>
  </Form.Group>
</Form>
        <Button variant="success" type="button" onClick={()=>{props.handlelogin(username,password);}}> Login</Button>
      
      

        {props.valid==="failed"?<div style={{color:"red"}}>Invalid UserName or Password </div>:null}
    </>

    )
}
export default Login