import React,{useContext} from 'react';

import BookContext from './BookContext'


export default function FormDialog(props:any) {
  const initialState =useContext(BookContext)

  const [anotherOtp,SetanotherOtp] = React.useState("");

  const handleSubmit = (e:any) => {
    SetanotherOtp(e.target.value);
  };

  const handleClose = () => {
    initialState.books.open=false;
    initialState.books.otp=false;

  };
 

  return (
    <div>
          <input
            autoFocus
            id="name"
            onChange={handleSubmit}
            placeholder="Enter Otp"
            type="text"
          />
          <button onClick={handleClose}>
            Cancel
          </button>
          <button onClick={()=>{handleClose();props.handlesomething(anotherOtp)}}>
            Submit
          </button>
    </div>
  );
}
